$main-color: #173660;
$bg-color: #f7f6ef;

$font-family-subheader: "Playfair Display", serif;

.card {
  position: relative;
  width: 250px;
  height: 250px;
  background: white;
  border-radius: 5px;
  box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.4);
  transition: box-shadow 0.5s, opacity 0.5s;
  will-change: transform;
  border: 10px solid white;
  cursor: grab;
  overflow: hidden;
  touch-action: none;
  padding-bottom: 3rem;
  z-index: 1005;

  &:hover {
    box-shadow: 0px 30px 100px -10px rgba(200, 197, 197, 0.5);
  }

  > div {
    will-change: transform;
    height: 100%;
    margin: 0;
    pointer-events: none;

    > * {
      height: 100%;
      background-size: cover;
      background-position: center center;
      margin: 0;
      pointer-events: none;
    }
  }

  @media (max-width: 768px) {
    width: 170px;
    height: 170px;
    padding-bottom: 2.3rem;
    border: 7px solid white;
  }

  @media (max-width: 480px) {
    width: 210px;
    height: 210px;
  }
}

.card-text {
  font-family: "Gloria Hallelujah", cursive;
  font-weight: bold;
  font-size: 0.75em;
  // text-align: right;

  @media (max-width: 768px) {
    font-size: 0.5em;
  }
}

.marquee-container {
  overflow: hidden; /* Hide the overflowing text */
  white-space: nowrap;
  margin: 2em 1em 0em 1em;
  // background-color: red;

  font-size: 1rem;

  @media (max-width: 768px) {
    margin: 1em 1em 0em 1em;
  }
}

.marquee {
  display: inline-block;
  // white-space: nowrap;
  // padding-left: 100%; /* Add padding to allow for spacing when it loops */
  animation: scroll 40s linear infinite;
  padding: 1em 1em 1em 6em;

  font-size: 1.25rem;

  @media (max-width: 768px) {
    animation: scroll 50s linear infinite;
    padding: 0.5em 0.5em 0.5em 3em;
    font-size: 0.6rem;
  }
}

@keyframes scroll {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.container {
  // background-color: rgb(173, 207, 255, 0.15);
  // background-color: rgb(0, 0, 0);
  background-color: #173660;

  margin: 0em 1em 0em 1em;
  padding: 1.5em 0em 1em 0em;
  display: flex;
  align-items: center;
  // height: 100%;
  justify-content: center;

  background-size: contain; /* Ensures the entire image fits within the div */
  background-position: center; /* Centers the image */
  background-repeat: no-repeat; /* Prevents tiling */
}

.deck {
  margin: 1rem 0rem 2rem 0rem;
  background-color: $bg-color;
  display: grid;
  grid-template-columns: repeat(4, 1fr);

  /* Responsive adjustments for mobile screens */
  @media (max-width: 1280px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 768px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media (max-width: 480px) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.stack {
  position: relative; /* Set the stacking context */
  display: inline-block; /* Keeps the wrapper as small as its content */
}

.secret-page-btn {
  position: absolute; /* Absolutely position the button */
  top: 50%; /* Center the button vertically */
  left: 50%; /* Center the button horizontally */
  transform: translate(-50%, -50%); /* Center by transforming it */
  z-index: 0; /* Behind the card */
  opacity: 1; /* Optional: Make the button semi-transparent */
  background-color: lightgray;
  color: $main-color;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  border-radius: 2px;
  text-decoration: none;
  white-space: no-wrap;
  text-align: center;
}

.secret-subheader {
  font-family: $font-family-subheader;
  font-size: 24px;
  text-align: center;
  width: 70%;
  padding: 0.75em 0em 0.5em 0em;
  margin: auto;
  margin-bottom: 2rem;
  // background-color: rgb(212, 214, 214);
  font-weight: lighter;

  @media (max-width: 768px) {
    font-size: 16px;
    margin-bottom: 2rem;
    padding: 0.5em 1.75em 0em 1.75em;
  }
}

.secret-body {
  font-size: 16px;
  letter-spacing: 1px;
  padding: 1em 1em;

  @media (max-width: 768px) {
    font-size: 12px;
    padding: 0.25em 1em;
  }
}

.letterpage-banner {
  background-size: contain; /* Ensures the entire image fits within the div */

  background-image: url("../media/ENVELOPE.png"), url("../media/Coraline_Stand.png"); /* Two images */
  background-position: left top, right top; /* Position one on the left, one on the right */
  background-repeat: no-repeat; /* Prevent repeat */

  @media (max-width: 480px) {
    background-image: url("../media/ENVELOPE.png"), url("../media/Coraline_Stand.png");
    background-position: left, right;
    background-repeat: no-repeat, no-repeat; /* Prevent repeat */
    background-size: 25%, 30%; /* Ensures the entire image fits within the div */
  }
}


.secretpage-banner {
  background-size: contain; /* Ensures the entire image fits within the div */

  background-image: url("../media/ENVELOPE.png"), url("../media/Coraline_Stand.png"); /* Two images */
  background-position: left top, right top; /* Position one on the left, one on the right */
  background-repeat: no-repeat; /* Prevent repeat */

  @media (max-width: 480px) {
    // background-image: none;
    background-image: url("../media/ENVELOPE.png"), url("../media/Coraline_Stand.png");
    background-position: left top, right top;
    background-repeat: no-repeat, no-repeat; /* Prevent repeat */
    background-size: 35%, 35%; /* Ensures the entire image fits within the div */
  }
}
