// Import Google Fonts
@import url("https://fonts.googleapis.com/css2?family=Tillana&family=Give+You+Glory&family=Sue+Ellen+Francisco&family=Annie+Use+Your+Telescope&family=Hi+Melody&family=Shadows+Into+Light&family=Ms+Madi&family=Nothing+You+Could+Do&family=Gloria+Hallelujah&family=Homemade+Apple&family=Indie+Flower&family=Sofadi+One&family=Playfair+Display&display=swap");

// Variables for Colors and Fonts
$main-color: #173660;
$bg-color: #f7f6ef;
$font-family-header: "Sofadi One", cursive;
$font-family-subheader: "Playfair Display", serif;
$font-family-letter-title: "Homemade Apple", cursive;
$font-family-letter-body: "Indie Flower", cursive;

// Specific Styles
.home-btn {
  position: absolute;
  color: $main-color;
  z-index: 10;
  // background-color: red;
  padding: 0.5em 1em 0em 1em;
  // font-family: $font-family-letter-body;
  // font-weight: bold;
  cursor: pointer;
  text-decoration: none;

  // Create a pseudo-element for the underline
  &::after {
    content: "";
    display: block;
    width: 0;
    height: 1.5px;
    background: $main-color; // Set the underline color
    transition: width 0.3s ease-in-out; // Control the transition
    margin-top: 0px; // Add some space between text and underline
  }

  &:hover {
    // Animate the underline width on hover
    opacity: 1;
    &::after {
      width: 100%; // Expand the underline to the full width of the text
    }
  }

  &:visited {
    color: $main-color; /* Ensure visited link has the same color */
  }
  
  @media  (max-width: 768px) {
    font-size: 12px;
  }
}

.share-btn {
  position: absolute;
  right: 2px;
}

.letterpage-header {
  // font-family: $font-family-header;
  // text-decoration: underline;
  font-size: 48px;
  text-align: center;
  font-weight: bold;
  font-style: italic;
  transform: scaleY(1.5);
  letter-spacing: 1.5px;

  padding: 1em 0em 0.5em 0em;
  margin-bottom: 10px;

  transition: 0.3s ease-in-out;

  @media  (max-width: 768px) {
    margin-top: 15px;
    font-size: 24px;
  }

}

.letterpage-subheader {
  font-family: $font-family-subheader;
  font-size: 24px;
  text-align: center;
  padding: 0.75em 0em 0.5em 0em;
  margin-bottom: 8rem;
  // background-color: rgb(212, 214, 214);
  font-weight: lighter;
  
  
  @media  (max-width: 768px) {
    font-size: 16px;
    margin-bottom: 2rem;
    padding: 0.5em 1.75em 0em 1.75em;
  }
}

.letterboard-grid {
  // display: grid;
  // grid-template-columns: repeat(3, 1fr);  // Creates 3 equal columns
  // grid-gap: 20px;                         // Adds spacing between grid items
  // justify-items: start;
  // grid-auto-flow: dense;
  margin-top: 1em;
  column-gap: 2em;
  padding: 0em 1em;
}

.letter {
  background-color: $bg-color;

  background-size: "cover";
  background-position: "center";
  background-repeat: "no-repeat";
  padding: 1rem 1rem;
  cursor: pointer;
  // border: 1px solid $main-color;

  // margin: 0.25rem 0.25rem;
  margin-bottom: 1em;

  // box-shadow: 8px 4px 6px rgb(23, 54, 96, 0.5);

  box-shadow: rgba(23, 54, 96, 0.5) 0px 3px 12px;

  break-inside: avoid;
  box-sizing: border-box;

  font-family: $font-family-letter-title;
  transition: 0.15s ease-in;

  &:hover {
    transform: translateX(-5px) translateY(-5px);
    box-shadow: 12px 8px 12px 5px rgb(23, 54, 96, 0.5);

  }

  .letter-title {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 10px;

    @media  (max-width: 768px) {
      font-size: 24px;
    }
  }

  .letter-body {
    font-size: 20px;
    font-weight: lighter;
    white-space: pre-wrap;
    
    @media  (max-width: 768px) {
      font-size: 16px;
    }
  }
  
  .letter-body.overlay-scroll {
    max-height: 70vh;
    overflow-y: auto;  

    scrollbar-width: thin; /* for Firefox */
    scrollbar-color: $main-color $bg-color; /* thumb color and track color */
  }
  
  .letter-footer {
    display: flex;
    flex-direction: row;
    font-family: $font-family-letter-body;
    font-weight: bold;
    white-space: pre-wrap;
    // background-color: red;
    justify-content: space-between;
    margin-top: 1.5rem;
    // .letter-sign {
    // }
    .time {
      display: flex;
      text-align: right;
    }
    
    @media  (max-width: 768px) {
      font-size: 12px;
    }
  }
}
.letter.no-hover:hover {
  transform: none; /* Disable hover scaling */
  box-shadow: none;
}


// The floating button style
.floating-button {
  position: fixed;
  z-index: 10;
  bottom: 20px;
  right: 20px;
  background-color: $main-color;
  color: white;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  cursor: pointer;
  // transition: width 0.3s ease, background-color 0.3s ease;
  transition: 0.25s ease-in;
  // transition-delay: 0.3s ease;

  &:hover {
    width: 120px;
    border-radius: 40px 40px;
    background-color: $main-color;
    justify-content: space-around;
    padding: 0 10px;
  }

  .plus-icon {
    transition: opacity 0.3s ease;
  }

  .button-text {
    visibility: hidden;
    position: absolute;
    // display: none;
    opacity: 0;
    font-size: 16px;
    transition: 0.25s ease-in-out;
  }

  &:hover .button-text {
    visibility: visible;
    opacity: 1;
    display: inline-block;
    transition-delay: 0.1s;
  }

  &:hover .plus-icon {
    display: none; // Hide the plus icon when hovered
  }
}

// Modal overlay (background becomes foggy)
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(247, 246, 239, 0.65);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

// The modal itself
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: $bg-color;
  padding: 50px 30px 30px 30px;
  border-radius: 5px;
  width: 400px;
  z-index: 1001;

  border: 1px solid gray;
  box-shadow: 5px 5px rgb(87, 86, 86);

  input,
  textarea {
    width: 95%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    justify-content: space-around;
  }

  .header-input {
    font-size: 18px;
    font-weight: bold;
  }

  .body-input {
    height: 150px;
  }

  .close-btn {
    position: absolute;
    text-align: center;
    width: 20px;
    height: 20px;
    top: 10px;
    right: 20px;
    cursor: pointer;
    font-size: 18px;
    color: $main-color;
    // background-color: #173660;
    padding: 5px;
    transition: 0.3s ease-in-out;

    &:hover {
      // background-color: red;
      color: red;
      font-weight: bold;
    }
  }

  .signature-input {
    margin: 2px 0px;
  }

  .post-btn {
    margin: 2px 0px;
    color: $main-color;
    background-color: $bg-color;
    padding: 0px 0px;
    border: solid 1px $main-color;
    border-radius: 5px;
    cursor: pointer;
    float: right;
    transition: 0.3s ease-in-out;

    &:disabled {
      cursor: not-allowed; // Change cursor when disabled
      opacity: 0.6; // Optional: Dim the button when disabled
      pointer-events: none;
    }
    &:not(:disabled):hover {
      background-color: #173660;
      color: white;
    }
  }
  
  /* Responsive adjustments for mobile screens */
  @media (max-width: 768px) {
    width: 50vw;  // Adjust font size for smaller screens
  }

  @media (max-width: 480px) {
    width: 60vw;  // Adjust font size for even smaller screens
  }
}

.non-interactable {
  pointer-events: none;  // Prevent any interaction
  opacity: 1;  // Reduce opacity to visually show it's disabled
}

.pagination {
  margin-top: 2rem;
  padding: 1rem 1rem 0rem 1rem;
  // background-color: #173660;
  display: flex;
  justify-content: space-around;
  color: #173660;
}

.confirm-box {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: $bg-color;
  padding: 2rem;
  border: 1px solid black;
  z-index: 1002;
