  }
}

.left, .right, .middle-top, .middle-bottom {
  background-size: contain; /* Ensures full background coverage */
  background-position: center;
  background-repeat: no-repeat;
}

.left {
  grid-column: 1; /* Left column */
  grid-row: 1 / span 2; /* Spans both rows */
}

.middle-top {
  grid-column: 2; /* Middle column */
  grid-row: 1; /* Top row */
}

.middle-bottom {
  grid-column: 2; /* Middle column */
  grid-row: 2; /* Bottom row */
  display: flex;
  align-items: center;
  justify-content: center;
}

.right {
  grid-column: 3; /* Right column */
  grid-row: 1 / span 2; /* Spans both rows */
}


.homepage-banner {
  
  background-size: contain;  /* Ensures the entire image fits within the div */
  // background-image: url('../media/frog_blue.png'), url('../media/frog_blue.png'); /* Two images */
  background-image: url('../media/Sticker_sketch_left.png'), url('../media/Sticker_sketch_right.png'); /* Two images */
  background-position: left top, right top; /* Position one on the left, one on the right */
  background-repeat: no-repeat; /* Prevent repeat */

  @media (max-width: 480px) {
    // background-image: none;
  }
}


/* Intro animation */
.intro {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $main-color; // Start with blue background
  color: $bg-color; // Start with red text
  font-size: 5rem;
  // font-size: 50vw;
  font-style: italic;
  z-index: 10;
  opacity: 1;
  animation: colorChange 2s forwards, fadeOut 1s 2s forwards; // Color change first, fade out after

  @keyframes colorChange {
    0% {
      background-color: $main-color;
      color: $bg-color;
    }
    100% {
      background-color: $bg-color;
      color: $main-color;
    }
  }

  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
      z-index: -1;
    }
  }
  /* Responsive adjustments for mobile screens */
  @media (max-width: 768px) {
    font-size: 8vw;  // Adjust font size for smaller screens
  }

  @media (max-width: 480px) {
    font-size: 10vw;  // Adjust font size for even smaller screens
  }
}

/* Keep main content hidden until intro fades out */
.hidden-content {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 1s, opacity 1s ease-in 1s; // Delay to match intro fade out timing
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.hidden-content.visible {
  visibility: visible;
  opacity: 1;
}

