// Variables for Colors and Fonts
$main-color: #173660;
$bg-color: #f7f6ef;
$scroll-bg-color: #d9e6f5;
// Global Styles
body {
  overflow-x: hidden;
  background-color: $bg-color;
  color: $main-color;
  user-select: none;


  scrollbar-width: thin; /* for Firefox */
  scrollbar-color: $main-color $bg-color; /* thumb color and track color */
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 12px; /* Width of the scrollbar */
}

::-webkit-scrollbar-track {
  background: $bg-color; /* Background of the scrollbar track */
}

::-webkit-scrollbar-thumb {
  background-color: $main-color; /* Color of the scrollbar thumb */
  // border-radius: 10px; /* Round edges of the thumb */
  border: 3px solid $bg-color; /* Padding around the thumb */
}

::-webkit-scrollbar-thumb:hover {
  background-color: #0e2439; /* Thumb color on hover */
}