$main-color: #173660;
$bg-color: #f7f6ef;

.sendletter-header {
  background-color: #173660;
  text-align: center;
  margin: 1rem;
  padding: 1rem;

  .letter-head {
    color: white;
    // width: 30rem;
    text-align: center;
    // background-color: red;
    margin: auto;
    font-family: "Gloria Hallelujah";
    font-size: 36px;
    font-weight: bold;
    padding: 0.5rem;
    white-space: no-wrap;
    @media (max-width: 768px) {
      padding-top: 0rem;
      // background-color: red;
      font-size: 24px;
    }
  }

  .letter-prompt {
    color: white;
    width: 50%;
    text-align: center;
    // background-color: red;
    margin: auto;
    font-size: 18px;
    font-family: "Gloria Hallelujah";
    @media (max-width: 768px) {
      width: 90%;
      font-size: 12px;
    }
  }
}

.card-bg {
  width: 20rem;
  height: 20rem;
  background-color: #f7f6ef;
  padding: 0.5rem 0.5rem 3rem 0.5rem;
  margin: auto;
  box-shadow: 5px 2px 10px gray;

  > * {
    height: 100%;
    background-color: white;
    text-align: center;

    background-size: cover;
    background-position: center center;
  }

  @media (max-width: 768px) {
    width: 14rem;
    height: 14rem;
  }
}

.sendletter-btn {
  //   position: fixed;
  //   top: 50%;
  //   left: 50%;
  //   transform: translate(-50%, -50%);
  margin: auto;

  width: 120px;
  border-radius: 40px 40px;
  // background-color: $main-color;
  border: solid 1px $main-color;
  color: $main-color;
  justify-content: space-around;
  text-align: center;
  padding: 1em 1em;
  transition: 0.25s ease-in-out;

  margin-bottom: 5rem;

  &:hover {
    background-color: $main-color;
    color: white;
    cursor: pointer;
  }
}

.post-message {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: $bg-color; /* Semi-transparent background */
  color: $main-color;
  padding: 1em 2em;
  border: solid 1px $main-color;
  box-shadow: 5px 5px 10px $main-color;
  border-radius: 5px;
  font-size: 1.5rem;
  opacity: 0;
  animation: fade-in-out 2s ease-in-out;
  z-index: 1000; /* Ensure it stays above everything */
}

@keyframes fade-in-out {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.back-to-home {
  bottom: 5rem;
  left: 50%;
  transform: translateX(-50%);
}